import React, {useState, useEffect} from 'react';
import Post from './post';
import EachPost from './eachpost';
import { HttpRequest } from '../../services/http-service/httpService';
import { useParams, Switch, Route, Link } from 'react-router-dom';
import add from '../../assets/icon/icons8-plus.svg';


function DiscussType(props) {
  let { id } = useParams();
  const [discuss_type, setDiscuss] = useState('music');
  const [posts, setPosts] = useState([]);

  // set page id each time page changes
  useEffect(() => {
    setDiscuss(id);
  });

  // only fetch api if page changes
  useEffect(() => {
    getPosts();
  },[discuss_type]);

  // Get Posts
  const getPosts = async () => {
    const url = new URL(`${process.env.REACT_APP_HOSTURL}/get_post/`);
    const posts = await HttpRequest.getPosts(url, discuss_type);
    setPosts(posts);
  }

  // Add post
  const addPost = () => {
    props.history.push(`/discuss/${discuss_type}/post`)
  };

  return (
    <>
      <section className="discuss_type" id="discuss_type">
        <div className="container">
          <div className="row justify-content-center">
            <div className="discuss_title col-10 col-md-8">
              <p className="type">{`${discuss_type.slice(0, 1).toUpperCase()}${discuss_type.substr(1)}`}</p>
              <span onClick={addPost} className="add"><img src={add}/></span>
            </div>
            {posts.map((post, index) => 
              <div className="discuss_post col-10 col-md-8" key={`${post.title}-${index}`}>
                <p className="title">{post.title}</p>
                <p className={`content ${post.content.length > 20 ? 'overflow': ''}`}>{post.content}</p>
                <div className="info">
                  <Link className="read_more" to={`/discuss/${discuss_type}/each?id=${post.id}`}>Read more</Link>
                  <p className="author">{post.user_info.username}</p>
                  <p className="time">{post.time.slice(0, 10)}</p>
                </div>
              </div>)}
          </div>
        </div>
      </section>
      <Switch>
        <Route path='/discuss/:id/each' component={EachPost}/>
        <Route path='/discuss/:id/post' component={Post}/>
      </Switch>
    </>
  )
}

export default DiscussType;