import React, { useState, useEffect } from 'react';
import Cookie from 'js-cookie';
import { HttpRequest } from '../../services/http-service/httpService';
import { useRouteMatch, useParams } from 'react-router-dom';
import '../../assets/sass/discuss/discuss.scss';

function Post(props) {
  let { path } = useRouteMatch();
  let { id } = useParams();
  const [user_info, setUser] = useState({
    'Ann': 1,
    'John': 2,
    'Judy': 3
  })
  const titleRef = React.createRef();
  const contentRef = React.createRef();
  
  useEffect(() => {
    if(path === '/discuss/:id/post'){
      document.getElementById('discuss_type').style.display = 'none';
    }
    return function cleanup() {
      document.getElementById('discuss_type').style.display = 'block';
    }
  });

  // Submist Post
  const submit = async() => {
    let currentTime = new Date();
    const post_info = {
      discuss_type : id,
      title : titleRef.current.value,
      content : contentRef.current.value,
      user_id : user_info[Cookie.get('userId')],
      time : currentTime.toISOString().replace(/T/, ' ').replace(/\..+/, ''),
    }
    // Save each post to DB
    const data = await HttpRequest.savePost(`${process.env.REACT_APP_HOSTURL}/save_post/`, post_info);
    if(data.status === 'ok' && data.isSaved === true){
      props.history.goBack();
    }
  }

  return(
    <section className="discuss_post">
      <div className="container">
        <div className="row justify-content-center">
          <form className="discuss_form col-10 col-md-6">
            <label className="label">Title</label>
            <input ref={titleRef} className="input"/>
            <label className="label">Content</label>
            <textarea ref={contentRef} className="textarea" rows="15"/>
            <a onClick={submit}>Submit</a>
          </form>
        </div>
      </div>
    </section>
  )
}

export default Post;