import React, { useState, useEffect } from 'react';
import { HttpRequest } from '../../services/http-service/httpService';
import { useParams, useRouteMatch } from 'react-router-dom';
import Cookie from 'js-cookie';
import '../../assets/sass/discuss/discuss.scss';

function EachPost(props) {
  let { id } = useParams();
  const path = useRouteMatch();
  const messageRef = React.createRef();
  const [ID, setID] = useState('1');
  const [user_info, setUser] = useState({'Ann': 1, 'John': 2,'Judy': 3})
  useEffect(() => { 
    const ID =  new URLSearchParams(props.location.search).get('id');
    setID(ID);
  });
  const [post, setPost] = useState({});
  const [comments, setComments] = useState([]);

  useEffect(() => {
    console.log('id is =>', id);
    // Get Each Post
    getEachPost();
    // Set Style
    if(path.path === '/discuss/:id/each'){
      document.getElementById('discuss_type').style.display = 'none';
    }
    return function cleanup() {
      document.getElementById('discuss_type').style.display = 'block';
    }
  }, [ID])

  // Get Each Post
  const getEachPost = async() => {
    const url = new URL(`${process.env.REACT_APP_HOSTURL}/get_eachpost/`);
    const eachpost_result = await HttpRequest.getEachPost(url, ID);
    setPost(eachpost_result.discuss);
    setComments(eachpost_result.comments);
  }

  // Leave Message
  const leaveMessage = async() => {
    const url = `${process.env.REACT_APP_HOSTURL}/leave_msg/`; 
    let currentTime = new Date();
    const msg_info = {
      comment : messageRef.current.value,
      time : currentTime.toISOString().replace(/T/, ' ').replace(/\..+/, ''),
      discuss_id : ID,
      user_id : user_info[Cookie.get('userId')],
    }
    console.log('msg_info is =>', msg_info);
    const message_result = await HttpRequest.leaveMessage(url, msg_info);
    if(message_result.isLeaveMsg === true){
      window.location.reload();
    };
  }
  return (
    <section className="discuss_content">
      <div className="container">
        <div className="row justify-content-center">
          {post.time && 
          <div className="col-10 col-md-8 content">
            <p className="title">{post.title}</p>
            <p className="post_content">{post.content}</p>
            <div className="post_info">
              <span className="author">{Object.keys(user_info)[post.user - 1]}</span>
              <span className="time">{post.time.slice(0, 10)}</span>
            </div>
          </div>}
          <div className="col-10 col-md-8 message">
            <p className="notify">Leave a message...</p>
            <textarea ref={messageRef} className="messages"></textarea>
            <button onClick={leaveMessage} className="submit">Submit</button>
          </div>
          <div className="col-10 col-md-8 comments">
            {comments && comments.map((item, index) => 
            <div key={`${item.comments}-${index}`} className="each_comment">
              <p className="comment_content">{item.comments}</p>
              <div className="comment_info">
                <p className="comment_owner">{Object.keys(user_info)[item.user - 1]}</p>
                <p className="comment_time">{item.time.slice(0, 10)}</p>
              </div>
            </div>)}
          </div>
        </div>
      </div>
    </section>
  )
}

export default EachPost;