import React from 'react';
import {Link, Route, Switch} from 'react-router-dom';
import DiscussType from './type';
import '../../assets/sass/discuss/discuss.scss';

class Discuss extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      discuss_type : ['sports', 'music', 'entertainment', 'finance'],
      type : 'sports'
    }
    this.selectType = this.selectType.bind(this);
  }
  componentDidMount(){
    this.props.history.push('/discuss/sports');
  }
  selectType(type){
    console.log('type is =>', type);
    this.setState({type : type});
  }
  render() {
    return(
      <div className="discuss main">
        <header className="header_background">

        </header>
        <section className="discuss_board">
          <div className="container">
            <div className="row justify-content-center">
              {this.state.discuss_type.map(type => 
                <Link 
                  className={`col-6 col-md-2 discuss_type ${this.state.type === type ? 'active': ''}`} 
                  key={type} 
                  to={`/discuss/${type}`}
                  onClick={this.selectType.bind(this, type)}>
                  {type}
                </Link>
              )}
            </div>
          </div>
        </section>
        <Switch>
          <Route path='/discuss/:id' component={DiscussType}/>
        </Switch>
      </div>
    )
  }
}

export default Discuss;